body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.inferno-logo {
  background: #fff;
  width: 6rem;
  height: 6rem;
  padding: 2rem;
  border-radius: 2rem;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #fff;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Roulette__roller {
  width: 100%;
  position: relative;
  padding: 5px 0;
  background-color: #0f1322;
  border: 3px solid #0f1322;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
}

.Roulette__roller-curtain {
  display: none;
  z-index: 9;
  position: absolute;
  background: rgba(15,19,34,.85);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.Roulette__roller-curtain.is--visible {
  display: block;
}

.Roulette__roller-curtain_text {
  position: absolute;
  left: 0;
  top: 50%;
  font-size: 21px;
  font-weight: 500;
  transform: translateY(-50%);
  display: flex;
  color: white;
}

@media screen and (min-width: 968px) {
  .Roulette__roller-curtain_text {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: white;
  }
}

.Roulette_indicator {
  width: 4px;
  background-color: #fff;
  position: absolute;
  left: 50%;
  margin-left: -2px;
  top: 0;
  bottom: 0;
  z-index: 2;
}

.Roulette_spin-container {
  z-index: 1;
  overflow: hidden;
  position: relative;
}

.Roulette_spin-items {
  display: flex;
  flex-direction: row;
  position: relative;
  text-align: center;
  width: 8450px;
  transform: matrix(1,0,0,1,0,0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.spin-item {
  flex: 0 0 auto;
  width: 70px;
  height: 70px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 2px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.spin-item span {
  display: none;
  font-size: 21px;
  flex: 0 0 auto;
  font-weight: 500;
}

.spin-item.red {
  background: url(/assets/image/red.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}

.spin-item.black {
  background: url(/assets/image/blue.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}

.spin-item.green {
  background: url(/assets/image/gold.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}
.Roulette__info {
  display: flex;
  margin-bottom: 10px;
  flex-direction: column;
}

@media screen and (min-width: 968px) {
  .Roulette__info {
    align-items: center;
    justify-content: flex-end;
    height: 29px;
  }
  .Roulette__bets, .Roulette__info {
    flex-direction: row;
  }
}

.Roulette__previous {
  flex: 1 1;
  opacity: .8;
  margin-left: 5px;
}

.Roulette__previous, .Roulette__previous__field {
  display: flex;
  align-items: center;
}

.Roulette__previous__field-image {
  height: 24px;
  width: 24px;
}

.Roulette__history {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

@media screen and (min-width: 968px) {
  .Roulette__history {
    margin: 0;
  }
}

.Roulette__history-text {
  font-weight: 500;
  font-size: 15px;
  color: #59679c;
  margin-right: 5px;
}

.Roulette__history-numbers {
  display: flex;
  flex-direction: row;
  position: relative;
  text-align: center;
  overflow: hidden;
  width: 240px;
}

.Roulette__history-icon {
  display: block;
  flex: 0 0 auto;
  width: 20px;
  height: 20px;
  margin: 0 2px;
}
.Roulette__holder {
  position: relative;
}

.Roulette_bett_balance {
  margin-left: 10px;
  display: flex;
  align-items: center;
}

@media screen and (min-width: 968px) {
  .Roulette_bett_balance {
    justify-content: center;
  }
}

.Roulette_bett_balance-text {
  font-weight: 700;
  margin-right: 10px;
}

.Roulette_bett_balance-flame {
  margin-left: 5px;
}

.Roulette__bett {
  display: flex;
  margin: 5px 0;
}

@media screen and (min-width: 968px) {
  .Roulette__bett {
    justify-content: center;
  }
}

.Roulette__bett_inside {
  margin: 10px;
  background: #0c0f1a;
  border: 1px solid #1d2134;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

@media screen and (min-width: 968px) {
  .Roulette__bett_inside {
    flex-direction: row;
    width: auto;
  }
}

.Roulette__bett_input {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #1d2134;
}

@media screen and (min-width: 968px) {
  .Roulette__bett_input {
    border-right: 1px solid #1d2134;
    border-bottom: none;
  }
}

.Roulette__bett_input-flame {
  margin: 12px 5px 12px 10px;
}

.Roulette__bett_input-field {
  flex-grow: 1;
  border: none;
  outline: none;
  resize: none;
  background: #0c0f1a;
  color: #fff;
  font-size: 1em;
}

.Roulette__previous__field-number {
  font-weight: 600;
  margin: 0 10px 0 5px;
}

.Roulette_bet_helpers {
  margin: 5px;
  display: flex;
  align-items: center;
}

.Roulette_bet_helper {
  -webkit-appearance: button;
  cursor: pointer;
  height: 30px;
  font-size: 14px;
  font-weight: 500;
  color: #8b95c7;
  background: 0 0;
  border: none;
  padding: 0 5px;
  transition: background 175ms,#fff 175ms;
}

@media screen and (min-width: 968px) {
  .Roulette_bet_helper {
    padding: 0 10px;
  }
}
.Roulette__bets-side {
  position: relative;
  margin: 10px 0;
}

@media screen and (min-width: 968px) {
  .Roulette__bets-side {
    flex: 1 1;
  }
  .Roulette__bets-side:nth-child(2) {
    margin: 10px 20px;
  }
}

.Roulette__side_button {
  cursor: pointer;
  position: relative;
  height: 3.125rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #272e42;
  border-width: 1px;
  border-color: transparent;
  box-shadow: 0 3px 0 0 #1d2233;
  border-radius: .4375rem;
  transition: background .25s ease;
}

.Roulette__side_button--coin {
  width: 40px;
  height: 40px;
  -webkit-filter: drop-shadow(0 2px 3px rgba(0,0,0,.6));
  filter: drop-shadow(0 2px 3px rgba(0,0,0,.6));
}

.Roulette__side_button--coin.color--blue {
  background: url(/assets/image/blue.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}

.Roulette__side_button--coin.color--gold {
  background: url(/assets/image/gold.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}

.Roulette__side_button--coin.color--red {
  background: url(/assets/image/red.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}

.Roulette__side_button__main {
  flex: 1 1;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  display: flex;
  align-items: center;
  margin: 0 10px;
}

.Roulette__side_button__main-text {
  letter-spacing: 1px;
  text-transform: capitalize;
  opacity: .9;
  font-size: 17px;
}

.Roulette__side_button__main-win {
  margin-left: auto;
  opacity: .7;
}

.Roulette__bets_main {
  display: flex;
  align-items: center;
  font-size: 14px;
  margin: 10px 2px;
  font-weight: 500;
  color: #8b95c7;
  height: 25px;
}

.Roulette__bets-divider {
  background: #1d2134;
  height: 2px;
  width: 100%;
  margin: 10px 0;
}

.Roulette__bets_collapse {
  font-size: 14px;
  font-weight: 600;
  color: #8b95c7;
  cursor: pointer;
  margin-bottom: 10px;
}

@media screen and (min-width: 968px) {
  .Roulette__bets_collapse {
    display: none;
  }
}

.Roulette__bets-side.is--collapsed>.Roulette__bets_players {
    display: none;
}

@media screen and (min-width: 968px) {
  .Roulette__bets-side.is--collapsed>.Roulette__bets_players {
    display: block;
  }
}

.Roulette__bets_player {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  transition: transform .15s ease-in,opacity .15s ease-in;
}

.Roulette__bets_player-profile {
  color: #fff;
  display: flex;
  align-items: center;
  color: #8b95c7;
}

.Roulette__bets_player-image {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-right: 5px;
}

.Roulette__bets_amount {
  color: #fff;
  font-weight: 500;
  margin-left: auto;
  display: flex;
  align-items: center;
}

.Roulette__bets_svg {
  margin-left: 5px;
}
.Game__submenu {
  padding: 10px 0 15px;
}

.Game__submenu, .Game__submenu-left, .Game__submenu-right {
  display: flex;
  align-items: center;
}

.Game__submenu, .Game__submenu-left, .Game__submenu-right {
  display: flex;
  align-items: center;
}

.Game__submenu-right {
  margin-left: auto;
}

.Game__submenu-item {
  display: flex;
  align-items: center;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-weight: 500;
  font-size: 14px;
  text-transform: capitalize;
  color: #979cba;
  padding: 3px 10px;
  border-radius: 3px;
  transition: all .1s linear;
}

.Game__submenu-right>.Game__submenu-item {
  margin-left: 10px;
}

.Game__submenu-item--icon {
  width: 16px;
  height: 16px;
  margin: 3px;
}

@media screen and (min-width: 960px) {
  .Game__submenu-item--icon {
    width: 18px;
    height: 18px;
    margin-right: 6px;
  }
}
.Roulette {
  margin: 0;
  padding: 0;
  font-family: Montserrat,sans-serif;
  background: #0f1322;
  color: #f4f4ff;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
}

.App-singlepage_holder {
  margin: 0 10px;
  line-height: 1.8;
}

.Roulette__bets {
    display: flex;
    flex-direction: column;
    margin: 15px 0;
}

@media screen and (min-width: 968px) {
  .Roulette__bets, .Roulette__info {
    flex-direction: row;
  }
}
